import GetStarted from "../../GetStarted";
import { useLocation, useNavigate } from "react-router-dom";
import { useState } from "react";
import InstituteRegisterType from "./InstituteRegisterType";
import InstituteRegisterMode from "./InstituteRegisterMode";
import InstituteRegisterForm from "./InstituteRegisterForm";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import { useAuthInstituteCreateAccount } from "../../../Hooks/ApiHooks/Auth/auth-api";
import { decryption } from "../../../Services/Encryption/decrypt";

const InstituteCreateAccount = () => {
  const navigate = useNavigate();
  const getQuery = useLocation();
  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [state, setState] = useState({
    insName: "",
    name: "",
    insEmail: "",
    insAddress: "",
    insPincode: "",
    insMode: "",
    insType: "",
  });

  const [authInstituteCreateAccount] = useAuthInstituteCreateAccount();
  const onRegisterType = (val) => {
    setState((prev) => ({
      ...prev,
      insType: val,
    }));
  };

  const onSubmitRegisterType = (e) => {
    e.preventDefault();
    let validation = requiredField({ insType: state.insType });
    if (existingRequiredField(validation)) {
      setStep("MODE");
    } else {
      setErrorField(validation);
    }
  };
  const onRegisterMode = (val) => {
    setState((prev) => ({
      ...prev,
      insMode: val,
    }));
  };

  const onSubmitRegisterMode = (e) => {
    e.preventDefault();
    let validation = requiredField({ insMode: state.insMode });
    if (existingRequiredField(validation)) {
      setStep("FORM_FIELD");
    } else {
      setErrorField(validation);
    }
  };

  const onChangeHandler = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onCreateAccount = (e) => {
    e.preventDefault();
    setDisabled((pre) => !pre);
    let validation = requiredField(state, ["insPincode"]);
    if (existingRequiredField(validation)) {
      setDisabled((pre) => !pre);
      authInstituteCreateAccount({
        profileCreation: {
          ...state,
          insPhoneNumber: getQuery?.state?.phoneNumber,
          insMobileStatus: getQuery?.state?.insMobileStatus,
        },
      })
        .then((res) => {
          let response = decryption(res?.data?.encrypt);
          if (response?.institute?._id) {
            navigate("/institute/create/account/password", {
              state: {
                id: response?.institute?._id,
              },
            });
          }
        })
        .catch({});
    } else {
      setErrorField(validation);
      setDisabled((pre) => !pre);
    }
  };
  return (
    <>
      <GetStarted>
        {!step ? (
          <InstituteRegisterType
            onGrab={onRegisterType}
            state={state.insType}
            onSubmit={onSubmitRegisterType}
            errorField={errorField}
          />
        ) : step === "MODE" ? (
          <InstituteRegisterMode
            onGrab={onRegisterMode}
            state={state.insMode}
            onSubmit={onSubmitRegisterMode}
            errorField={errorField}
          />
        ) : (
          <InstituteRegisterForm
            onChangeHandler={onChangeHandler}
            state={state}
            onSubmit={onCreateAccount}
            errorField={errorField}
            disabled={disabled}
          />
        )}
      </GetStarted>
    </>
  );
};

export default InstituteCreateAccount;
