import { useTranslation } from "react-i18next";
import style from "../../Auth.module.css";
import { assestsAuthUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import ButtonWithTypeSubmit from "../../../Ui/Button/ButtonWithTypeSubmit";

const InstituteRegisterMode = ({ state, onGrab, onSubmit, errorField }) => {
  const { t } = useTranslation();
  return (
    <div className={style.auth_card_wrapper}>
      <div className={style.auth_create_container}>
        <div>
          <h6
            className={style.auth_card_title}
            style={{
              fontSize: "1rem",
            }}
          >
            {t("type_of_ins")}
          </h6>
          <h6
            className={style.auth_card_subtitle}
            style={{
              fontSize: "0.8rem",
            }}
          >
            {t("choose_others")}
          </h6>
        </div>
      </div>
      <div
        className={style.auth_login_form}
        style={{
          marginTop: "0",
        }}
      >
        <form onSubmit={onSubmit}>
          <div className={style.institute_type_container}>
            {modeType?.map((rgtype) => (
              <div
                onClick={() => onGrab(rgtype.key)}
                className={style.institute_type_card}
              >
                <img
                  src={
                    state === rgtype.key
                      ? `${assestsAuthUrl}/${rgtype.activeIcon}`
                      : `${assestsAuthUrl}/${rgtype.greyIcon}`
                  }
                  alt="type icon"
                />
                <p
                  style={{
                    color: state === rgtype.key ? "#34495e" : "#95a5a6",
                  }}
                >
                  {t(rgtype.name)}
                </p>
              </div>
            ))}
          </div>

          <ButtonWithTypeSubmit
            type="submit"
            buttonText={t("next")}
            customStyleButton={{
              marginTop: "1.5rem",
            }}
          />
        </form>
      </div>
    </div>
  );
};

export default InstituteRegisterMode;

const modeType = [
  {
    name: "online",
    key: "Online",
    greyIcon: "online.svg",
    activeIcon: "online-dark.svg",
  },
  {
    name: "offline",
    key: "Offline",
    greyIcon: "offline.svg",
    activeIcon: "offline-dark.svg",
  },
  {
    name: "both",
    key: "Both",
    greyIcon: "both.svg",
    activeIcon: "both-dark.svg",
  },
];
